<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText
              title="ФИО"
              placeholder="Введите ваше полное имя"
              v-model="formData.name"
              :error="formError.name" />

            <BaseFormText
            title="Адрес доставки"
            :error="formError.address"
            v-model="formData.address"
            placeholder="Введите ваш адрес" />

            <BaseFormText
            title="Телефон"
            :error="formError.phone"
            v-model="formData.phone"
            placeholder="Введите ваш телефон" />

            <BaseFormText
            title="Email"
            :error="formError.email"
            v-model="formData.email"
            placeholder="Введи ваш Email" />

            <BaseFormTextArea
            title="Комментарий к заказу"
            :error="formError.comment"
            v-model="formData.comment"
            placeholder="Ваши пожелания"/>

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>
        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="product in products" :key="product.productId">
              <h3>{{ product.product.title }}</h3>
              <b>{{ product.product.price | numberFormat }} ₽</b>
              <span>Артикул: {{ product.productId }}</span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ totalProductsAmount }}</b> товара на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>
          <div v-if="orderLoading">
          <loader object="#ff9633" color1="#ffffff" color2="#17fd3d" size="5" speed="2" bg="#343a40" objectbg="#999793" opacity="80" disableScrolling="false" name="circular"></loader>
          Отправляем заказ...
          </div>
          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea.vue';
import { API_BASE_URL } from '@/config';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import axios from 'axios';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      fromErrorMessage: '',
      orderLoading: false,
    };
  },
  filters: { numberFormat },
  components: { BaseFormText, BaseFormTextArea },
  methods: {
    order() {
      this.formError = {};
      this.fromErrorMessage = '';
      this.orderLoading = true;
      setTimeout(() => {
        axios
          .post(`${API_BASE_URL}/api/orders`, {
            ...this.formData,
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          })
          .then((response) => {
            this.orderLoading = false;
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);        
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          })
          .catch(error => {
            this.orderLoading = false;
            this.formError = error.response.data.error.request || {};
            this.fromErrorMessage = error.response.data.error.message;
          });
      }, 1000);      
    },
  },
  computed: {
    ...mapGetters({ products: 'cartDetailsProducts', totalPrice: 'cartTotalPrice', totalProductsAmount: 'cartProductsAmount' }),
  },
};
</script>

